import classNames from 'classnames';

//主组件
const Transitions2D = (props) => {
	const prefixCls = 'ani-2d';
	const {
		className,
		type = 'grow',
	} = props;
	const classString = classNames(
		className,
		prefixCls,
		`${prefixCls}-${type}`,
	);
	return <span className={classString}>{props.children}</span>;
}

export default Transitions2D
