import Transitions2D from '../../../cssAnimation/Transitions2D/Transitions2D.jsx'

const Demo = () => {
	return <div className="note-example-box">
		<div className="note-example-intro">正方体展示</div>
		<div className="aninote-example-box">
			<div>
				<Transitions2D type="grow">grow</Transitions2D>
				<Transitions2D type="shrink">shrink</Transitions2D>
				<Transitions2D type="pulse">pulse</Transitions2D>
				<Transitions2D type="push">push</Transitions2D>
			</div>
			<div style={{'marginTop': '10px'}}>
				<Transitions2D type="bounce-in">bounce-in</Transitions2D>
				<Transitions2D type="bounce-out">bounce-out</Transitions2D>
			</div>
			<div style={{'marginTop': '10px'}}>
				<Transitions2D type="skew-forward">skew-forward</Transitions2D>
				<Transitions2D type="skew-backward">skew-backward</Transitions2D>
				<Transitions2D type="wobble-horizontal">wobble-horizontal</Transitions2D>
			</div>
		</div>
	</div>;
}


export default Demo
